<template lang="pug">
  .support-contacting
    form.support-contacting__form(@submit.prevent="submitHandler")
      .form__block
        h4.form__subtitle Обратная связь
        email-field(id="email" v-model="email" :v="$v.email" :class="{checked: $v.email.required && $v.email.email}")
        name-field(id="firstName" v-model="name" :v="$v.name")
      .form__block
        h4.form__subtitle Текст обращения
        .support-contacting__wrap
          textarea.support-contacting-text--textarea(v-model="message" placeholder="введите текст обращения")
      .registration__action
        button-hover(tag="button" type="submit" variant="white") Отправить
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'
  import {required, email, minLength, maxLength} from 'vuelidate/lib/validators'
  import EmailField from '@/components/FormElements/EmailField'
  import NameField from '@/components/FormElements/NameField'


  export default {
    name: "ContactingSupport",
    components: {
      EmailField,
      NameField
    },
    data: () => ({
      email: '',
      name: '',
      message: ''
    }),
    methods: {
      ...mapActions('support/api', ['sendMessage']),
      submitHandler() {
        if (this.$v.$invalid) {
          this.$v.$touch()
          return
        }
        const { email, name, message } = this
        this.sendMessage({ email, name, message }).then(() => {
          this.$router.push({ name: 'ContactingSupportSuccess' })
        })
      }
    },
    validations: {
      email: { required, email },
      name: { required, minLength: minLength(3), maxLength: maxLength(25) }
    }
  }
</script>

<style lang="stylus">
  @import '../../assets/stylus/base/vars.styl';
  .support-contacting__wrap {
    display: flex;
    width: 100%;
    max-width: 460px;
    padding: 10px 0px;
  }
  .support-contacting-text {
    width: 100%;
    height: 45px;
    border: 1px solid #E3E3E3;
    padding: 0 20px;
    font-size: 15px;
    color: #000;
    cursor: pointer;
    display: flex;
    align-items: center;
    white-space: nowrap;
    overflow: hidden;
    position: relative;

    &::placeholder {
      color: #B0B0BC;
    }

    &--textarea {
      padding: 15px 20px;
      min-height: 300px;
      width: 100%;
      white-space: normal;
    }
  }
</style>



